:root {
  --root-font-size: 16px;
  --ff-primary: "Montserrat", serif;
  --fs-100: 0.8125rem;
  --fs-200: 0.875rem;
  --fs-300: 0.9375rem;
  --fs-400: 1rem;
  --fs-500: 1.125rem;
  --fs-600: 1.25rem;
  --fs-700: 1.375rem;
  --fs-800: 1.5rem;
  --fs-900: 1.625rem;
  --fs-1000: 2em;
  --fs-1500: 3.25rem;
  --fw-thin: 100;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;
  --fw-extra-bold: 800;
  --lh-100: 0.75;
  --lh-200: 1;
  --lh-300: 1.25;
  --lh-400: 1.5;
  --lh-500: 2;
  --c-primary: #009ffe;
  --c-primary-rgb-10: rgb(41 148 255 / 20%);
  --c-secondary: #bdd2e7;
  --c-black: #17171a;
  --c-black-alt: #0a0a0a;
  --c-white: #fefefe;
  --c-grey-100: #343434;
  --c-grey-200: #1e1e23;
  --c-grey-300: #878787;
  --c-grey-400: #bebebe;
  --c-grey-500: #dcdcdc;
  --c-gradient: #ff5733 0%, #FFC300 100%;
  --w-100: 480px;
  --w-200: 640px;
  --w-300: 800px;
  --w-400: 960px;
  --w-500: 1120px;
  --w-600: 1280px;
  --w-700: 1440px;
  --w-800: 1600px;
  --w-900: 1760px;
  --w-1000: 1920px;
  --w-spacing: calc(100% - 2em);
  --t-fast: 0.3s;
  --t-medium: 0.5s;
  --t-slow: 1s;
}
:root .width-100 {
  width: min(var(--w-100), var(--w-spacing));
}
:root .width-200 {
  width: min(var(--w-200), var(--w-spacing));
}
:root .width-300 {
  width: min(var(--w-300), var(--w-spacing));
}
:root .width-400 {
  width: min(var(--w-400), var(--w-spacing));
}
:root .width-500 {
  width: min(var(--w-500), var(--w-spacing));
}
:root .width-600 {
  width: min(var(--w-600), var(--w-spacing));
}
:root .width-700 {
  width: min(var(--w-700), var(--w-spacing));
}
:root .width-800 {
  width: min(var(--w-800), var(--w-spacing));
}
:root .width-900 {
  width: min(var(--w-900), var(--w-spacing));
}
:root .width-1000 {
  width: min(var(--w-1000), var(--w-spacing));
}

.button-container {
  display: flex;
  gap: 1em;
}
.button-container.button-container--align-left {
  justify-content: flex-start;
}
.button-container.button-container--align-center {
  justify-content: center;
}
.button-container.button-container--align-right {
  justify-content: end;
}
@media (max-width: 800px) {
  .button-container.button-container--vw300-mobile {
    flex-direction: column;
  }
}
@media (max-width: 640px) {
  .button-container.button-container--vw200-mobile {
    flex-direction: column;
  }
}
@media (max-width: 480px) {
  .button-container.button-container--vw100-mobile {
    flex-direction: column;
  }
}